<template lang="pug">
  .PrologueUniComponent__page-content
    .PrologueUniComponent__reset-password-wr
      .PrologueUniComponent__reg
        .PrologueUniComponent__reg-recovery-title
          | Для восстановления я укажу:
        .PrologueUniComponent__reg-type-btns
          button.PrologueUniComponent__reg-type-btn.PrologueUniComponent__reg-type-btn--active.js-PrologueUniComponent__reg-type-btn-rec(type='button' data-btn-row-id='phone')
            | Номер телефона
          button.PrologueUniComponent__reg-type-btn.js-PrologueUniComponent__reg-type-btn-rec(type='button' data-btn-row-id='email')
            | Email
        .PrologueUniComponent__reg-form-wr
          form.PrologueUniComponent__reg-form(@submit.prevent="resetPassword")
            .PrologueUniComponent__reg-form-position
              input.js-PrologueUniComponent__reg-form-rec(type='hidden' name='currentTypeRecovery' value='phone')
              .PrologueUniComponent__reg-form-input-row.PrologueUniComponent__reg-form-input-row--active(data-row-id='phone' @click="isPhone = true")
                .PrologueUniComponent__reg-form-input-top
                  .PrologueUniComponent__reg-form-input-top-left
                    SvgIcon.PrologueUniComponent__reg-form-input-top-ico(name="PrologueUniComponent-phone" width="14" height="14")
                  .PrologueUniComponent__reg-form-input-top-right
                    | Телефон
                .PrologueUniComponent__reg-form-input-bottom
                  input.PrologueUniComponent__reg-form-input.js-PrologueUniComponent__reg-form-input-phone(type='text' name='phone' placeholder='+7 (___)-___-__-__' required v-model="phoneInput")
              .PrologueUniComponent__reg-form-input-row(data-row-id='email' @click="isPhone = false")
                .PrologueUniComponent__reg-form-input-top
                  .PrologueUniComponent__reg-form-input-top-left
                    SvgIcon.PrologueUniComponent__reg-form-input-top-ico(name="PrologueUniComponent-email" width="14" height="14")
                  .PrologueUniComponent__reg-form-input-top-right
                    | Email
                .PrologueUniComponent__reg-form-input-bottom
                  input.PrologueUniComponent__reg-form-input(type='email' name='email' placeholder='' v-model="emailInput")
            .PrologueUniComponent__reg-form-btns
              button.PrologueUniComponent__reg-form-btn-submit.PrologueUniComponent__reg-form-btn-submit--black.js-PrologueUniComponent__reg-form-btn-back(type='button' @click="$router.go(-1)")
                span Назад
              button.PrologueUniComponent__reg-form-btn-submit(type='submit')
                span Отправить
</template>

<script>
  import EventBus from '@/eventBus';
  import prologueClient from 'prologue-client'

  import SvgIcon from '@/components/common/svgIcon/SvgIcon.vue';

  // @ is an alias to /src
  import '@/components/common/popups/popupMessage/popupMessage.styl';

  import {AjaxHelpersVue} from '@/mixins/AjaxHelpersVue';
  import {PopupHelpersVue} from '@/mixins/PopupHelpersVue';

  export default {
    name: 'PasswordReset',

    components: {
      SvgIcon
    },

    data() {
      return {
        phoneInput: '',
        emailInput: '',
        isPhone: true,
        formShow: true
      }
    },

    created() {
      this.PrologueUniComponent_formPhoneInit()
    },

    methods: {
      clearForm() {
        this.phoneInput = ''
        this.emailInput = ''
      },

      resetPassword() {
        if (this.isPhone) {
          prologueClient.queryToServer('Passport:resetPassword', {
            phone: this.phoneInput
          }, (serverData) => {
            if (serverData.status === 'ok') {
              EventBus.$emit('formShow', this.formShow);
              this.clearForm()

              setTimeout(() => {
                EventBus.$emit('formReshow', this.formShow);
                this.$router.push({path: '/passport/login'});
              }, 2000)
            }
          });
        } else {
          prologueClient.queryToServer('Passport:resetPassword', {
            email: this.emailInput
          }, (serverData) => {
            if (serverData.status === 'ok') {
              EventBus.$emit('formShow', this.formShow);
              this.clearForm()

              setTimeout(() => {
                EventBus.$emit('formReshow', this.formShow);
                this.$router.push({path: '/passport/login'});
              }, 2000)
            }
          });
        }
      },

      PrologueUniComponent_formPhoneInit() {
        $(document).ready(function($) {
          $('.js-PrologueUniComponent__reg-form-input-phone').mask('+7(000)-000-00-00');
        })
      },
    }
  }
</script>
