<template lang="pug">
  .PrologueUniComponent__page-content
    transition(appear name="show")
    transition(name="fade" mode="out-in")
      .PrologueUniComponent__page-content-inside(v-show="formShow")
        .PrologueUniComponent__h1-title
          | {{titleChange}}

        .PrologueUniComponent__reg
          .PrologueUniComponent__reg-type-btns(v-if="$route.name !== 'PasswordReset'")
            router-link.PrologueUniComponent__reg-type-btn(to="/passport/registration" active-class="PrologueUniComponent__reg-type-btn--active")
              | Регистрация
            router-link.PrologueUniComponent__reg-type-btn(to="/passport/login" active-class="PrologueUniComponent__reg-type-btn--active")
              | Авторизация

          router-view

          .PrologueUniComponent__reg-socials(v-if="$route.name !== 'PasswordReset'")
            .PrologueUniComponent__reg-socials-title
              | Быстрая авторизация через
            .PrologueUniComponent__reg-socials-items(v-if="socials")
              a.PrologueUniComponent__reg-socials-item(v-for="item in socials" :href='item.link' :key="item.id" target='_blank')
                img.PrologueUniComponent__reg-socials-item-img(:src='item.ico' alt='')

          .PrologueUniComponent__reg-no-acc(v-if="$route.name === 'Authorization'")
            | Нет аккаунта? Пройдите быструю #[router-link(to="/passport/registration") регистрацию]!

    .PrologueUniComponent__reg-success-message-wr-wr(v-show="!formShow && $route.name !== 'PasswordReset'")
      transition(appear name="show")
        RegSuccess

    .PrologueUniComponent__password-send-message-wr-wr(v-show="!formShow && $route.name === 'PasswordReset'")
      transition(appear name="show")
        PasswordSend



</template>

<script>
  import EventBus from '@/eventBus';
  import prologueClient from 'prologue-client'
  import SvgIcon from '@/components/common/svgIcon/SvgIcon.vue';
  import RegSuccess from '@/components/common/regSuccess/regSuccess.vue';
  import PasswordSend from '@/components/common/passwordSend/passwordSend.vue';

  export default {
    name: 'Passport',

    data() {
      return {
        socials: [],
        formShow: true
      }
    },

    components: {
      SvgIcon,
      RegSuccess,
      PasswordSend
    },

    computed: {
      titleChange() {
        if (this.$route.name === 'Registration') {
          return 'Регистрация'
        } else if (this.$route.name === 'Authorization') {
          return 'Авторизация'
        } else if (this.$route.name === 'PasswordReset') {
          return 'Восстановление пароля'
        }
      }
    },

    created() {
      prologueClient.middleware.isUserNoAuth();
      this.getSocials()
    },

    mounted() {
      this.PrologueUniComponent_formPasswordInit();
      this.PrologueUniComponent_formPhoneInit();
      this.PrologueUniComponent_formRecoveryInit();

      EventBus.$on('formShow', () => {
        this.formShow = false
      });

      EventBus.$on('formReshow', () => {
        this.formShow = true
      });
    },

    methods: {
      getSocials() {
        prologueClient.queryToServer('PassportPage:getSocials', {}, (serverData) => {
          this.socials = serverData.socials
        });
      },

      PrologueUniComponent_formPasswordInit() {
        $(document).on('click', '.js-PrologueUniComponent__reg-form-input-password', function() {
          $(this)
            .parents('.PrologueUniComponent__reg-form-input-bottom')
            .find('.PrologueUniComponent__reg-form-input-popup')
            .addClass('PrologueUniComponent__reg-form-input-popup--show');
        });
      },

      PrologueUniComponent_formPhoneInit() {
        $(document).ready(function($) {
          $('.js-PrologueUniComponent__reg-form-input-phone').mask('+7(000)-000-00-00');
        })
      },

      PrologueUniComponent_formRecoveryInit() {
        $(document).on('click', '.js-PrologueUniComponent__reg-type-btn-rec', function() {
          var rowId = $(this).attr('data-btn-row-id') || '';

          $(this).parents('.PrologueUniComponent__reg').find('.PrologueUniComponent__reg-type-btn--active')
            .removeClass('PrologueUniComponent__reg-type-btn--active');
          $(this).addClass('PrologueUniComponent__reg-type-btn--active');

          $('.PrologueUniComponent__reg-form-input-row--active').removeClass('PrologueUniComponent__reg-form-input-row--active');
          $('[data-row-id="' + rowId + '"]').addClass('PrologueUniComponent__reg-form-input-row--active');

          $('.js-PrologueUniComponent__reg-form-rec').val(rowId);

          $(this).parents('.PrologueUniComponent__reg').find('input[required]').removeAttr('required');
          $('[data-row-id="' + rowId + '"]').find('input').attr('required', true);
        });
      }
    }

  }
</script>
