<template lang="pug">
  header.PrologueUniComponent__header
    MobileMenu
    Burger
    .PrologueUniComponent__wr
      .PrologueUniComponent__header-line-wr
        .PrologueUniComponent__header-content
          .PrologueUniComponent__header-left
            router-link.PrologueUniComponent__header-logo(:to='header.logo.link')
              img.PrologueUniComponent__header-logo-img(:src='header.logo.src' alt='')

          .PrologueUniComponent__header-right
            .PrologueUniComponent__header-nav
              router-link.PrologueUniComponent__header-nav-item(v-for="item in header.menu" :to='item.link' :key="item.id" active-class="PrologueUniComponent__header-nav-item--active")
                | {{item.title}}

        .PrologueUniComponent__header-nav-line
          .PrologueUniComponent__header-nav-line-track
            .PrologueUniComponent__header-nav-line-block

</template>

<script>
  // import '@/components/common/header/header.styl';

  import EventBus from '@/eventBus';
  import {AjaxHelpersVue} from '@/mixins/AjaxHelpersVue';
  import SvgIcon from '@/components/common/svgIcon/SvgIcon.vue';

  import MobileMenu from '@/components/common/mobileMenu/mobileMenu';
  import Burger from '@/components/common/burger/burger';

  export default {
    name: 'Header',

    data() {
      return {

      }
    },

    props: ['header'],

    components: {
      MobileMenu,
      Burger,
      SvgIcon
    },

    created() {
      this.PrologueUniComponent_header()
    },

    methods: {
      PrologueUniComponent_header() {
        setTimeout(function() {
          initMenuTopLine();
        }, 100);

        function initMenuTopLine() {
          if($('.PrologueUniComponent__header-nav').length) {
            setMenuToActiveEl();

            $(document).on('mouseenter', '.PrologueUniComponent__header-nav-item', function() {
              setMenuTopLine(this);
            });

            $(document).on('mouseleave', '.PrologueUniComponent__header-right', function() {
              setMenuToActiveEl();
            });
          }
        }

        function setMenuTopLine(el) {
          var elWidth     = $(el).width();
          var leftWrapper = $('.PrologueUniComponent__header-nav-line').offset().left;
          var left        = $(el).offset().left - leftWrapper;
          var $lineBlock  = $('.PrologueUniComponent__header-nav-line-block');

          $lineBlock.css({
            transform: 'translateX(' + left + 'px)',
            width: elWidth
          });
        }

        function setMenuToActiveEl() {
          var $activeMenu = $('.PrologueUniComponent__header-nav-item--active');
          var elWidth     = $activeMenu.width();
          var leftWrapper = $('.PrologueUniComponent__header-nav-line').offset().left;
          var left        = $activeMenu.offset().left - leftWrapper;
          var $lineBlock  = $('.PrologueUniComponent__header-nav-line-block');

          $lineBlock.css({
            transform: 'translateX(' + left + 'px)',
            width: elWidth
          });
        }
      }
    }

  }
</script>
