<template lang="pug">
  #app.page(v-on:click="globalClick($event)" v-if="data")
    .PrologueUniComponent__page
      .PrologueUniComponent__matrix-top
        Matrix
      .PrologueUniComponent__matrix-bottom
        Matrix
      .PrologueUniComponent__page-gradient-1
      .PrologueUniComponent__page-gradient-2
      SideMenu(v-if="data.userData.isAuth === 1")
      .PrologueUniComponent__content
        Header(v-if="data.header" :header="data.header")
        transition(appear name="show-page")
          router-view
        Footer

</template>

<script>
  import EventBus from '@/eventBus';
  import SvgIcon from '@/components/common/svgIcon/SvgIcon.vue';
  import prologueClient from 'prologue-client'

  import '@/components/app/app.styl';

  import Header from '@/components/common/header/Header';
  import Footer from '@/components/common/footer/Footer';
  import SideMenu from '@/components/common/sideMenu/sideMenu';
  import Matrix from '@/components/common/matrix/matrix';

  export default {
    name: 'App',

    data() {
      return {
        data: null
      }
    },

    components: {
      SideMenu,
      Header,
      Footer,
      SvgIcon,
      Matrix
    },

    created() {

      prologueClient.init(
        {
          'mode': 'demo',
          'servers': {
            'demo': '/api/',
            'work': '/api/site/v1/'
          },
          'appRoutes': {
            'mainPage': '/',
            'authorizationPage': '/passport/login/',
            'userPersonalPage': '/personal/',
          }
        }
      );

      this.loadApp()


      this.$nextTick(() => {

        // Matrix
        if (process.env.NODE_ENV === 'production') {
          $('body').append('<script src="/app/interface/site/v1/js/matrix.js"><\/script>');
        }
        if (process.env.NODE_ENV === 'development') {
          $('body').append('<script src="/js/matrix.js"><\/script>');
        }

        // Jquery mask
        if (process.env.NODE_ENV === 'production') {
          $('body').append('<script src="/app/interface/site/v1/js/jquery.mask.min.js"><\/script>');
        }
        if (process.env.NODE_ENV === 'development') {
          $('body').append('<script src="/js/jquery.mask.min.js"><\/script>');
        }

      })
    },

    methods: {
      loadApp() {
        prologueClient.loader.loadApp({
          'route': 'MainModule:loadApp',
          'timeOut': 100,
        }, (appData) => {
          // App is load! Use appData
          EventBus.$emit('app', appData);
          this.ping()
          this.data = appData
        });
      },

      ping() {
        prologueClient.loader.ping({
          'route': 'MainModule:ping',
          'timeInterval': 30000,
        }, (serverResponse) => {
          // Use serverResponse
        });
      },

      // Закрыть попап по клику вне его
      globalClick(e) {
        let str = $(e.target).attr('class') || '';

        // Закрыть поиск
        // if(str.indexOf('search__wr') < 0) {
        //   EventBus.$emit('searchClose');
        //   EventBus.$emit('resetOpenSearchBtn');
        // }
      }
    }
  }
</script>
