<template lang="pug">
    .PrologueUniComponent__reg-form-wr
      form.PrologueUniComponent__reg-form(@submit.prevent="setAuthInfo")
        .PrologueUniComponent__reg-form-input-row
          .PrologueUniComponent__reg-form-input-top
            .PrologueUniComponent__reg-form-input-top-left
              SvgIcon.PrologueUniComponent__reg-form-input-top-ico(name="PrologueUniComponent-email" width="11" height="11")
            .PrologueUniComponent__reg-form-input-top-right
              | Email
          .PrologueUniComponent__reg-form-input-bottom
            input.PrologueUniComponent__reg-form-input(type='email' required name='email' placeholder='example@example.ru' v-model="emailInput")
        .PrologueUniComponent__reg-form-input-row
          .PrologueUniComponent__reg-form-input-top
            .PrologueUniComponent__reg-form-input-top-left
              SvgIcon.PrologueUniComponent__reg-form-input-top-ico(name="PrologueUniComponent-key" width="11" height="11")
            .PrologueUniComponent__reg-form-input-top-right
              | Пароль
          .PrologueUniComponent__reg-form-input-bottom
            .PrologueUniComponent__reg-form-input-popup
              .PrologueUniComponent__reg-form-input-popup-txt
                | Авторизируйтесь для того, чтобы продолжить. Введите пароль, высланый вам на почту, и нажмите кнопку
              button.PrologueUniComponent__reg-form-input-popup-submit(type='submit')
                span Войти

            input.PrologueUniComponent__reg-form-input.js-PrologueUniComponent__reg-form-input-password(type='password' required name='password' v-model="passwordInput")

        .PrologueUniComponent__reg-form-btns
          button.PrologueUniComponent__reg-form-btn-submit(type='submit')
            span Войти
          router-link.PrologueUniComponent__reg-form-btn-reg(to="/passport/forgot-password")
            | Забыли пароль?

</template>

<script>
  import EventBus from '@/eventBus';
  import prologueClient from 'prologue-client'
  import SvgIcon from '@/components/common/svgIcon/SvgIcon.vue';

  import '@/components/registration/registration.styl';

  export default {
    name: 'Authorization',

    data() {
      return {
        emailInput: '',
        passwordInput: '',
        appData: null
      }
    },

    components: {
      SvgIcon
    },

    mounted() {

      EventBus.$on('app', data => {
        this.appData = data
      });

      this.PrologueUniComponent_formPasswordInit();
      this.PrologueUniComponent_formPhoneInit();
      this.PrologueUniComponent_formRecoveryInit();
    },

    methods: {
      clearForm() {
        this.emailInput = ''
        this.passwordInput = ''
      },

      setAuthInfo() {
        prologueClient.passport.login(
          {
            'route': 'Passport:login',
            'credentials': {
              email: this.emailInput,
              password: this.passwordInput
            }
          },
          (loginResult) => {

            if (loginResult.access.isLoginSuccess) {
              this.clearForm()
              this.loadApp()

              setTimeout(() => {
                this.$router.push({path: '/personal'});
              }, 1000)
            }


          }
        );
      },

      loadApp() {
        prologueClient.loader.loadApp({
          'route': 'MainModule:loadApp',
          'timeOut': 100,
        }, (appData) => {
          // App is load! Use appData
        });
      },

      PrologueUniComponent_formPasswordInit() {
        $(document).on('click', '.js-PrologueUniComponent__reg-form-input-password', function() {
          $(this)
            .parents('.PrologueUniComponent__reg-form-input-bottom')
            .find('.PrologueUniComponent__reg-form-input-popup')
            .addClass('PrologueUniComponent__reg-form-input-popup--show');
        });
      },

      PrologueUniComponent_formPhoneInit() {
        $(document).ready(function($) {
          $('.js-PrologueUniComponent__reg-form-input-phone').mask('+7(000)-000-00-00');
        })
      },

      PrologueUniComponent_formRecoveryInit() {
        $(document).on('click', '.js-PrologueUniComponent__reg-type-btn-rec', function() {
          var rowId = $(this).attr('data-btn-row-id') || '';

          $('.PrologueUniComponent__reg-form-input-row--active').removeClass('PrologueUniComponent__reg-form-input-row--active');
          $('[data-row-id="' + rowId + '"]').addClass('PrologueUniComponent__reg-form-input-row--active');

          $('.js-PrologueUniComponent__reg-form-rec').val(rowId);

          $(this).parents('.PrologueUniComponent__reg').find('input[required]').removeAttr('required');
          $('[data-row-id="' + rowId + '"]').find('input').attr('required', true);
        });
      }
    }

  }
</script>
