<template lang="pug">
  .PrologueUniComponent__search-content-wr
    .PrologueUniComponent__search-result
      // json {"status": "no-items"}
      .PrologueUniComponent__search-result-message
        img.PrologueUniComponent__search-result-message-img(src='/img/content/face.svg' alt='')
        .PrologueUniComponent__search-result-message-title
          | Поиск не дал результатов...
      .PrologueUniComponent__search-result-preloader
        img.PrologueUniComponent__search-result-preloader-img(src="/img/content/logo-part.svg" alt="")
      .PrologueUniComponent__search-result-items
        //router-link.search__result-item(v-for="item in items", :to="item.link", :key="item.id", target="_blank")
          //.search__result-item-left
            img.search__result-item-img(:src="item.img", :alt="item.title")
          .search__result-item-right
            .search__result-item-title
              | {{item.title}}
            .search__result-item-txt
              | {{item.txt}}

    .PrologueUniComponent__search-content-wr-wr
      .PrologueUniComponent__search-content
        SvgIcon.PrologueUniComponent__search-content-ico(name="PrologueUniComponent-search" width="14" height="14")
        input.PrologueUniComponent__search-content-input.js-PrologueUniComponent__search-content-input(type='text' name='search' placeholder='Поле ввода' data-url='/json/search.json')

</template>

<script>
  import '@/components/common/search/search.styl';

  import EventBus from '@/eventBus';

  import {AjaxHelpersVue} from '@/mixins/AjaxHelpersVue';
  import {CommonHelpersVue} from '@/mixins/CommonHelpersVue';
  import SvgIcon from '@/components/common/svgIcon/SvgIcon.vue';

  export default {
    name: 'Search',

    mixins: [AjaxHelpersVue, CommonHelpersVue],

    data() {
      return {
      }
    },

    components: {
      SvgIcon
    },

    mounted() {
      var ps = null;

      function searchReq(str) {
        var url     = $('.js-PrologueUniComponent__search-content-input').attr('data-url') || '';
        var $loader = $('.PrologueUniComponent__search-result-preloader');

        $loader.removeClass('PrologueUniComponent__search-result-preloader--hide');

        setTimeout(function() {
          $.ajax({
            url: url,
            type: 'get',
            dataType: 'json',
            data: {
              action: 'getSearchResult',
              string: str
            }
          })
            .done(function(res) {
              $('.PrologueUniComponent__search-result-message')
                .removeClass('PrologueUniComponent__search-result-message--show');

              setTimeout(function() {
                $loader.addClass('PrologueUniComponent__search-result-preloader--hide');
              }, 250);

              if(res.status == 'ok') {
                searchTemplate(res.data);

                if(ps) {
                  ps.destroy();
                  ps = null;
                }

                ps = new PerfectScrollbar('.PrologueUniComponent__search-result-items', {
                  wheelSpeed: 0.25,
                  wheelPropagation: true,
                  minScrollbarLength: 20
                });

                $('.PrologueUniComponent__search-result-items').scrollTop(0);
              }

              if(res.status == 'no-items') {
                $('.PrologueUniComponent__search-result-message')
                  .addClass('PrologueUniComponent__search-result-message--show');
              }

              $('.PrologueUniComponent__search-content-wr')
                .addClass('PrologueUniComponent__search-content-wr--open');
            });
        }, 250);
      }

      function searchTemplate(data) {
        var $block = $('.PrologueUniComponent__search-result-items');
        var tmpl   = '';

        if(data.length) {
          data.forEach(function(el) {
            tmpl += `
          <a class="PrologueUniComponent__search-result-item" href="${el.link}" target='_blank'">
            <div class="PrologueUniComponent__search-result-item-title">${el.title}</div>
            <div class="PrologueUniComponent__search-result-item-txt">${el.txt || ''}</div></a>
        `;
          })
        }

        $block
          .empty()
          .append(tmpl);
      }

      $(document).on('click', function(e) {
        if(!$(e.target)
          .is('.PrologueUniComponent__search-content-wr, .PrologueUniComponent__search-content-wr *')) {
          $('.PrologueUniComponent__search-content-wr--open')
            .removeClass('PrologueUniComponent__search-content-wr--open');

          $('.PrologueUniComponent__search').removeClass('PrologueUniComponent__search--clicked');

        }
      });

      $(document)
        .on('input', '.js-PrologueUniComponent__search-content-input', this.debounce(function() {
          var str = $(this).val() || '';

          searchReq(str);
        }, 250));

      $(document)
        .on('click', '.js-PrologueUniComponent__search-content-input', function() {
          if($(window).width() >= 1260) {
            $(this).parents('.PrologueUniComponent__search').addClass('PrologueUniComponent__search--clicked');
          }
        });
    },

    methods: {

    }

  }
</script>
