<script>
  import SvgIcon from '@/components/common/svgIcon/SvgIcon.vue';
  import '@/components/common/sideMenu/sideMenu.styl';
  import {CommonHelpersVue} from '@/mixins/CommonHelpersVue';

  export default {
    name: 'SideMenu',

    data() {
      return {}
    },

    mixins: [CommonHelpersVue],

    components: {
      SvgIcon
    },

    mounted() {
      var ps = null;
      var data = ""

      function getSideMenuParams() {
        var projectType = $('.js-PrologueUniComponent__side-menu-project-content-active-btn.PrologueUniComponent__side-menu-project-content-active-btn--active').attr('data-value') || '';
        var searchStr = $('.js-PrologueUniComponent__side-menu-project-content-search-content-input').val() || '';

        data = {
          projectType: projectType
        };

        if(searchStr.length) {
          data['searchStr'] = searchStr;
        }

        return data;
      }

      function searchSideMenuReq(params) {
        var url     = $('.js-PrologueUniComponent__side-menu-project-content-search-content-input').attr('data-url') || '';
        var $loader = $('.PrologueUniComponent__side-menu-project-content-search-result-preloader');

        data = params;

        data['action'] = 'getProjects';

        $loader.removeClass('PrologueUniComponent__side-menu-project-content-search-result-preloader--hide');

        setTimeout(function() {
          $.ajax({
            url: url,
            type: 'get',
            dataType: 'json',
            data: data
          })
            .done(function(res) {
              $('.PrologueUniComponent__side-menu-project-content-search-message')
                .removeClass('PrologueUniComponent__side-menu-project-content-search-message--show');

              setTimeout(function() {
                $loader.addClass('PrologueUniComponent__side-menu-project-content-search-result-preloader--hide');
              }, 250);

              if(res.status == 'ok') {
                searchSideMenuTemplate(res.data);

                if(ps) {
                  ps.destroy();
                  ps = null;
                }

                ps = new PerfectScrollbar('.PrologueUniComponent__side-menu-project-content-search-result-items', {
                  wheelSpeed: 0.25,
                  wheelPropagation: true,
                  minScrollbarLength: 20
                });

                $('.PrologueUniComponent__side-menu-project-content-search-result-items').scrollTop(0);
              }

              if(res.status == 'no-items') {
                $('.PrologueUniComponent__side-menu-project-content-search-message')
                  .addClass('PrologueUniComponent__side-menu-project-content-search-message--show');
              }
            });
        }, 250);
      }

      function searchSideMenuTemplate(data) {
        var $block = $('.PrologueUniComponent__side-menu-project-content-search-result-items-scroll');
        var tmpl   = '';

        if(data.length) {
          data.forEach(function(el) {
            var tmplItem = '';

            tmpl += `
              <div class="PrologueUniComponent__side-menu-project-content-search-result-item">
                <div class="PrologueUniComponent__side-menu-project-content-search-result-item-cat-title">${el.category.title}</div>
                <div class="PrologueUniComponent__side-menu-project-content-search-result-item-projects">`;

            if(el.category.projects) {
              el.category.projects.forEach(function(item) {
                tmplItem += `<a class="PrologueUniComponent__side-menu-project-content-search-result-item-project" href="${item.link}">${item.title}</a>`;
              });
            }

            tmpl += tmplItem + `</div>
              </div>
            `;
          })
        }

        $block
          .empty()
          .append(tmpl);
      }

      $(document).on('click', '.js-PrologueUniComponent__side-menu-project-title', function() {
        //if(!isOpening) {

        //isOpening = true;

        $(this)
          .parents('.PrologueUniComponent__side-menu-project')
          .toggleClass('PrologueUniComponent__side-menu-project--open');

        /*setTimeout(function() {
          isOpening = false;
        }, 1000);*/

        if($(this)
          .parents('.PrologueUniComponent__side-menu-project')
          .hasClass('PrologueUniComponent__side-menu-project--open')) {

          searchSideMenuReq(getSideMenuParams());
        }
        else {
          setTimeout(function() {
            $('.PrologueUniComponent__side-menu-project-content-search-result-preloader')
              .removeClass('PrologueUniComponent__side-menu-project-content-search-result-preloader--hide');
          }, 250);
        }
        //}
      });

      $(document)
        .on('input', '.js-PrologueUniComponent__side-menu-project-content-search-content-input', this.debounce(function() {
          searchSideMenuReq(getSideMenuParams());
        }, 250));

      $(document).on('click', '.js-PrologueUniComponent__side-menu-project-content-active-btn', function() {
        $('.PrologueUniComponent__side-menu-project-content-active-btn--active')
          .removeClass('PrologueUniComponent__side-menu-project-content-active-btn--active');

        $(this)
          .addClass('PrologueUniComponent__side-menu-project-content-active-btn--active');

        searchSideMenuReq(getSideMenuParams());
      });

      // this.PrologueUniComponent_searchSideMenu();
      this.PrologueUniComponent_navSideMenu();
      this.PrologueUniComponent_navSideMenuScroll();
      this.PrologueUniComponent_navSideMenuHide();
    },

    methods: {
      PrologueUniComponent_navSideMenuHide() {
        if($(window).width() < 1260) {
          $(document).on('click', '.js-PrologueUniComponent__side-menu-show-btn', function() {
            $(this).toggleClass('PrologueUniComponent__side-menu-show-btn--show');

            $('.PrologueUniComponent__side-menu')
              .toggleClass('PrologueUniComponent__side-menu--show');
            $('body').toggleClass('overflow');
          });

          /*if($('.PrologueUniComponent__content-left').length) {
            new PerfectScrollbar('.PrologueUniComponent__content-left', {
              wheelSpeed: 0.25,
              wheelPropagation: true,
              minScrollbarLength: 20
            });
          }*/
        }
      },

      PrologueUniComponent_navSideMenuScroll() {
        var sideMenuHeight = $('.PrologueUniComponent__side-menu').outerHeight();
        var userBlockHeight = $('.PrologueUniComponent__side-menu-user').outerHeight();
        var menuBottom = $('.PrologueUniComponent__side-menu-bottom').outerHeight();

        var height = sideMenuHeight - userBlockHeight - menuBottom - 97;

        $('.PrologueUniComponent__side-menu-nav').css({
          height: height
        });

        new PerfectScrollbar('.PrologueUniComponent__side-menu-nav', {
          wheelSpeed: 0.25,
          wheelPropagation: true,
          minScrollbarLength: 20
        });
      },

      PrologueUniComponent_navSideMenu() {
        $(document).on('click', '.js-PrologueUniComponent__side-menu-nav-block-title-wr', function() {
          var $parent = $(this).parents('.PrologueUniComponent__side-menu-nav-block');

          $parent.toggleClass('PrologueUniComponent__side-menu-nav-block--open');


          $parent.find('.PrologueUniComponent__side-menu-nav-block-items').stop().slideToggle(function() {

            //$parent.toggleClass('PrologueUniComponent__side-menu-nav-block--open-progress');

          });
        });
      }
    }
  }
</script>
