import Vue from 'vue';
import Router from 'vue-router';
import Main from './views/main/Main';
import Passport from './views/passport/Passport';
import Registration from './views/passport/registration/Registration';
import Authorization from './views/passport/authorization/Authorization';
import PasswordReset from './views/passport/passwordReset/PasswordReset';
import Personal from './views/personal/Personal';
import Page404 from './views/Page404';

Vue.use(Router);

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'Main',
      component: Main
    },
    {
      path: '/passport',
      name: 'Passport',
      component: Passport,
      children: [
        {
          path: 'registration',
          name: 'Registration',
          component: Registration
        },
        {
          path: 'login',
          name: 'Authorization',
          component: Authorization
        },
        {
          path: 'forgot-password',
          name: 'PasswordReset',
          component: PasswordReset
        }
      ]
    },
    {
      path: '/personal',
      name: 'Personal',
      component: Personal
    },
    {
      path: '*',
      name: 'Page404',
      component: Page404
    }
  ]
})
